<template>
    <div>
        <ul class="plugin-list">
            <li>
                <h3>Name</h3>
                <div class="text">DatePicker</div>
            </li>
            <li>
                <h3>Introduction</h3>
                <div class="text"></div>
            </li>
            <li>
                <h3>Demo</h3>
                <p class="btn" @click.stop="onClickDate()">demo</p>
                <div class="text">
                    <input type="text" readonly="readonly" v-model="nowVal">
                </div>
            </li>
            <li>
                <h3>Function</h3>

<pre class="JS">
new DatePicker({
    //0, 1, 2, 3(3)
    "type": "3",

    //
    "title": '',

    //
    "maxYear": "2100",

    //
    "minYear": "1900",

    //()('/')
    "separator": "-",

    //
    "defaultValue": '',

    "callBack": function (val) {
        //val
        self.nowVal = val;
    }
});
</pre>
            </li>
            <li>
                <h3>Usage</h3>
                <div class="text">
                    </br>
                    'datePicker/picker.css'</br>
                    'datePicker/datePicker.min.js'
                </div>
            </li>
        </ul>
    </div>
</template>

<script>
    import DatePicker from '../../lib/datePicker/datePicker'

	export default {
		name: 'datePicker',
        data(){
            return {
                nowVal: ''
            }
        },
        methods: {
            // 
            onClickDate(){
                var self = this;
                new DatePicker({
                    "type": "3",//0, 1, 2, 3(3)
                    "title": '',//()
                    "maxYear": "",//
                    "minYear": "",//
                    "separator": "-",//()('/')
                    "defaultValue": self.nowVal,//
                    "callBack": function (val) {
                        //val
                        self.nowVal = val;
                    }
                });
            }
        }
	}
</script>

<style scoped>
    /*app.vue */
    /*@import "../../lib/datePicker/picker.css";*/
</style>


